import React, { Component } from 'react'

export default class color extends Component {
 
    componentDidMount() { this.getMinicolrData()}

    getMinicolrData=()=>{ 
        const { id, defaultColor , changeColor} = this.props
        var self = this;
        setTimeout(() => {
            window.$(`#${id}`).minicolors({
                control: 'hue',
                defaultValue: defaultColor,
                letterCase: 'lowercase',
                position: 'bottom left',
                change: self.onChangeColor,
                theme: 'bootstrap'
            })
        }, 1000)
    }


    onChangeColor = (hex, opacity) => {
        if (!hex) return; 
        this.props.changeColor(hex)
    }

    render() {
        const { id, defaultColor , changeColor} = this.props
        return (
            <input  className="form-control minicolors-input" data-control="hue"   type="text" id={id} defaultValue={defaultColor} />
        )
    }
}
