import HttpClient from "./http-client"
import { ADD_BACKGROUND, ON_SITE } from './api';


export const addBackground = (obj) => {
    return new HttpClient().post(`${ADD_BACKGROUND}/${localStorage.getItem('active_wall')}/${obj.themeId}/${obj.id}/${localStorage.getItem('website_id')}`, { ...obj })
}


export const onSiteUpload = (obj) => {
    return new HttpClient().post(`${ON_SITE}`, { ...obj, onsite_status: 1 })
}