import React, {Component } from 'react'
import { WEBSITE_EMBED_URL } from '../../../../actions/api';

export default class Preview extends Component{
    render(){
        return(
            <iframe src={`${WEBSITE_EMBED_URL}${localStorage.getItem('website_id')}/editor?website=true`} style={{width:'100%',height:'600px', border:'none'}}></iframe>
        )
    }
}