import moment from 'moment'
import React, { Component } from 'react'
import { connect } from 'react-redux'
import ReactTooltip from 'react-tooltip' 
import Swal from 'sweetalert2'
import { PUBLISH_SHOPIFY_GALLERY_DELETE } from '../../../../actions/productAction'
import HomeEmbed from './homeEmbed' 
import { DELETE_SHOPIFY_PUBLISHED, PUBLISH_SHOPIFY_GALLERY } from '../../../../actions/shopifyAction'
import { DELETE_BIGCOMMERCE_PUBLISHED, PUBLISH_BIGCOMMERCE_GALLERY } from '../../../../actions/bicommerceAction'
 class BigcommercePublish extends Component {
    state={ showPage:true, showSectionBrand:false, showSectionCategory:false, theme:"", page:"", sectionBrand:"",sectionCategory:"", position:"top", wallId:localStorage.getItem('wallId'), themeData:false, pageData:false, sectionData:false, error: false, galleryName: ''}

    

    componentDidMount(){
        const {BigcommerceData } = this.props 
        var storThemes=[]
        var Pages=[] 
        var themeSection=[]
        if(BigcommerceData && BigcommerceData.pagesArr){
                // Object.entries(BigcommerceData.storThemes[0]).map(([key, value]) => ( storThemes.push({key:key, value:`${value.dawn},${value.id}`}) ))
                Object.entries(BigcommerceData.pagesArr).map(([key, value]) => ( Pages.push({key:value, value:`${key}`}) )) 

                this.setState({themeData:storThemes, pageData:Pages, theme:storThemes && storThemes.length ? storThemes[0].value:'', page:Pages && Pages.length ? Pages[0].value:'', section:BigcommerceData.themeSection && BigcommerceData.themeSection.length ? BigcommerceData.themeSection[0].section:'' })
        }

      
    }


    onChangePages=(e)=>{
        if(e.target.value.includes('category_page')) this.setState({page:e.target.value, showSectionCategory:true})
        else this.setState({page:e.target.value, showSectionCategory:false})
        if(e.target.value.includes('brand_page')) this.setState({page:e.target.value, showSectionBrand:true})
        else this.setState({page:e.target.value, showSectionBrand:false})
    }

    publishGallery =()=>{
    const {theme,page, position, wallId, showSectionBrand,showSectionCategory,galleryName, sectionCategory, sectionBrand } = this.state
    const { BigcommerceData } = this.props
        
        var obj = {
            page,
            wallId: localStorage.getItem('website_id'),
            gallery_name: this.getWebsiteName(),
            brand_id: showSectionBrand ? sectionBrand ? sectionBrand : Object.keys(BigcommerceData.brands)[0] : '',
            category_id: showSectionCategory ? sectionCategory ? sectionCategory : Object.keys(BigcommerceData.categories)[0] : '',
            position: position
        }
       this.props.PUBLISH_BIGCOMMERCE_GALLERY(obj)
    // }
        
    }
   
    deleteGallery=(id)=>{

        Swal.fire({
            title: "Are you sure?",
            icon: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes",
            cancelButtonText: "No",
        }).then((result) => {
            if (result.value) {
                var obj = new FormData()
                obj.append("id", id)
                this.props.DELETE_BIGCOMMERCE_PUBLISHED(id)
            }
            else if (result.dismiss === "cancel") { }
        });

        
    }
    getWebsiteName = () => {
        const { websiteList } = this.props
             
        const websiteId = localStorage.getItem('website_id');
        if(websiteList && websiteList.length){
            const websiteName = websiteList.find(w => w.wall.Wall.id == websiteId)
             
            return websiteName.wall.Wall.name; 
        }
        return ''
    }

  render() {
    const {BigcommerceData } = this.props
    const {theme,page, sectionCategory, sectionBrand,  position, wallId , error, galleryName, showSectionCategory, showSectionBrand,  themeData, pageData, sectionData} = this.state
    this.getWebsiteName()
    return (
        <div className='tab-pane fade active show' id='shopifyPublish' role='tabpanel' aria-labelledby='shopifyPublish-tab'>
            <div className='card card-custom mb-5'>
                <div className='card-body pt-lg-5 pb-lg-2'>
                    <div className='row'>
                        <div className='col-lg-6 border-right-lg'>
                        <HomeEmbed />
                        </div>
                        <div className='col-lg-6'>
                            <div id='ShopifyIndexForm' className=''>
                                <div className='pl-lg-5 pr-lg-5'>
                                    <div className='form-group mb-5'>
                                        <h3 className='mb-1 font-weight-boldest'>Automatic Publishing </h3>
                                        <p>From here you can publish your gallery directly onto your BigCommerce store.</p>
                                    </div>
                                    <input type='hidden' name='' />
                                    <div className='form-group'>
                                        <div className='row'>
                                            <div className='col-xxl-6 col-lg-12 col-md-6 col-sm-12'>
                                                <div className='form-group'>
                                                    <label className='text-dark font-weight-bold'>
                                                        Website Name
                                                    </label>
                                                    <input type='text' className='form-control' value={this.getWebsiteName()} readOnly/>
                                                    {/* {error ? <span id="" className="errorMessage required">This field is required</span> : <></>} */}
                                                </div>
                                            </div>
                                            <div className='col-xxl-6 col-lg-12 col-md-6 col-sm-12'>
                                                <div className='form-group'>
                                                    <label className='text-dark font-weight-bold'>
                                                        Select Your Landing Page (Loaction)
                                                    </label>
                                                    <select className='form-control kt-selectpicker change-theme-page'  onChange={e=> this.onChangePages(e)}>
                                                        <option  value="" disabled='disabled'>Select Page</option>
                                                        {/* {BigcommerceData && BigcommerceData.pages ? Object.entries(BigcommerceData.pages).map(([key, value]) => (
                                                             <option key={key} value={key,value}>{key}</option>
                                                        )):''
                                                       } */}
                                                       {pageData && pageData.map((item, i)=>{
                                                          return <option key={i} value={item.value}>{item.key} </option>
                                                       })}
                                                    </select>
                                                </div>
                                            </div>
                                            {showSectionCategory ?
                                            <div className='col-xxl-6 col-lg-12 col-md-6 col-sm-12 dawn-theme-sections'>
                                                <div className='form-group'>
                                                    <label className='text-dark font-weight-bold'>Choose The Category</label>
                                                    <select className='form-control kt-selectpicker'  onChange={e=> this.setState({sectionCategory:e.target.value})}>
                                                        <option value="" disabled='disabled'>Select Category</option>
                                                        {BigcommerceData && BigcommerceData.categories ?  Object.entries(BigcommerceData.categories).map(([key, value]) => (
                                                             <option key={key} value={key}>{value} </option>
                                                        )):''
                                                       }
                                                       {/* {sectionData && sectionData.map((item, i)=>{
                                                          return <option key={i} value={item.value}>{item.key} </option>
                                                       })} */}
                                                    </select>
                                                </div>
                                            </div>:""}
                                            {showSectionBrand ?
                                            <div className='col-xxl-6 col-lg-12 col-md-6 col-sm-12 dawn-theme-sections'>
                                                <div className='form-group'>
                                                    <label className='text-dark font-weight-bold'>Choose The Brand</label>
                                                    <select className='form-control kt-selectpicker'  onChange={e=> this.setState({sectionBrand:e.target.value})}>
                                                        <option value="" disabled='disabled'>Select Brand</option>
                                                        {BigcommerceData && BigcommerceData.brands ?  Object.entries(BigcommerceData.brands).map(([key, value]) => (
                                                             <option key={key} value={key}>{value} </option>
                                                        )):''
                                                       }
                                                       {/* {sectionData && sectionData.map((item, i)=>{
                                                          return <option key={i} value={item.value}>{item.key} </option>
                                                       })} */}
                                                    </select>
                                                </div>
                                            </div>:""}
                                            <div className='col-xxl-6 col-lg-12 col-md-6 col-sm-12'>
                                                <p className='text-dark font-weight-bold'>Select The Position of Gallery on Page</p>
                                                <div className='d-flex align-items-center embed_position flex-wrap'>
                                                <div className='mr-5'>
                                                        <label className={`option option-plain flex-column ${position == 'top' ? 'active-position' : ''}`} htmlFor='position-top'>
                                                            <input type='radio' name='' id='position-top' defaultValue={position == 'top'}  defaultChecked={position == 'top'} onClick={() => this.setState({position: 'top'})} />
                                                            <span className='option-label py-3'>
                                                                <svg xmlns="http://www.w3.org/2000/svg" width="95" height="62" viewBox="0 0 95 62">
                                                                    <g id="Group_7039" data-name="Group 7039" transform="translate(-20 -290)">
                                                                        <rect id="Rectangle_2449" data-name="Rectangle 2449" width="95" height="10" rx="3" transform="translate(20 342)" fill="#1a2029" opacity="0.15"></rect>
                                                                        <rect id="Rectangle_2452" data-name="Rectangle 2452" width="95" height="30" rx="3" transform="translate(20 290)" fill="#1a2029" opacity="0.299"></rect>
                                                                        <rect id="Rectangle_2450" data-name="Rectangle 2450" width="95" height="10" rx="3" transform="translate(20 326)" fill="#1a2029" opacity="0.15"></rect>
                                                                    </g>
                                                                </svg>
                                                            </span>
                                                            <p className='text-center mb-0 font-weight-bold'>Top</p>
                                                        </label>
                                                    </div>
                                                    <div className='mr-5'>
                                                        <label className={`option option-plain flex-column ${position == 'bottom' ? 'active-position' : ''}`}  htmlFor='position-bottom'>
                                                            <input type='radio' name='' id='position-bottom' defaultValue={position == 'bottom'} defaultChecked={position == 'bottom'} onClick={() => this.setState({position: 'bottom'})} />
                                                            <span className='option-label py-3'>
                                                                <svg id='Group_7040' data-name='Group 7040' xmlns='http://www.w3.org/2000/svg' width='95' height='62' viewBox='0 0 95 62'>
                                                                    <rect id='Rectangle_2449' data-name='Rectangle 2449' width='95' height='10' rx='3' fill='#1a2029' opacity='0.15'></rect>
                                                                    <rect id='Rectangle_2452' data-name='Rectangle 2452' width='95' height='30' rx='3' transform='translate(0 32)' fill='#1a2029' opacity='0.299'></rect>
                                                                    <rect id='Rectangle_2450' data-name='Rectangle 2450' width='95' height='10' rx='3' transform='translate(0 16)' fill='#1a2029' opacity='0.15'></rect>
                                                                </svg>
                                                            </span>
                                                            <p className='text-center mb-0 font-weight-bold'>Bottom</p>
                                                        </label>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className='form-group border-top mt-lg-18'>
                                        <div className='mb-lg-0 d-flex justify-content-end w-100 my-0 pt-5'>
                                            <button  onClick={()=> this.publishGallery()} className='btn btn-primary btn-md'>PUBLISH GALLERY</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="card card-custom mb-10 show">
                <div className="card-body p-5">
                    <div className="p-lg-5 py-lg-4">
                        <div className="form-group">
                            <h3 className="mb-1">Manage Published Galleries</h3>
                            <p className="mb-2">Please Note: Manually installed plugins will not appear in this list</p>
                        </div>
                        <div className="table-responsive">
                            <table className="table table-bodered">
                                <thead>
                                    <tr>
                                        <th>Page</th>
                                        <th>Position</th>
                                        <th>Date Installed</th>
                                        <th className="text-right">Preview</th>
                                        <th className="text-right">Action</th>
                                    </tr>
                                </thead>
                                <tbody>
                               
                                    {BigcommerceData && BigcommerceData.embedData && BigcommerceData.embedData.length ?  BigcommerceData.embedData.map((item,i)=>{
                                      return <tr key={i}>
                                        <td>{item.pageTitle}</td>
                                        <td>{item.position}</td>
                                        <td className="text-nowrap">{moment(item.createdAt).format('MMMM DD, YYYY, hh:mm:ss A')} </td>
                                        <td className="text-right">
                                            <a href={`//${item.domain}`} target="_blank" className="btn btn-link btn-icon btn-clean mx-1">
                                                <span className="svg-icon-col svg-icon-2x">
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                        <g id="Group_4540" data-name="Group 4540" transform="translate(9376 -4253)">
                                                            <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-9376 4253)" fill="none" opacity="0"></rect>
                                                            <g id="Group_4497" data-name="Group 4497" transform="translate(-9374 4258)">
                                                                <path id="Path_5232" data-name="Path 5232" d="M21.87,11.5c-.64-1.11-4.16-6.68-10.14-6.5C6.2,5.14,3,10,2.13,11.5a1,1,0,0,0,0,1c.63,1.09,4,6.5,9.89,6.5h.25c5.53-.14,8.74-5,9.6-6.5A1,1,0,0,0,21.87,11.5ZM12.22,17c-4.31.1-7.12-3.59-8-5,1-1.61,3.61-4.9,7.61-5,4.29-.11,7.11,3.59,8,5-1.03,1.61-3.61,4.9-7.61,5Z" transform="translate(-1.996 -4.996)" fill="#545454"></path>
                                                                <path id="Path_5233" data-name="Path 5233" d="M12,8.5A3.5,3.5,0,1,0,15.5,12,3.5,3.5,0,0,0,12,8.5Z" transform="translate(-1.996 -4.996)" fill="#545454"></path>
                                                            </g>
                                                        </g>
                                                    </svg>
                                                </span>
                                            </a>
                                        </td>
                                        <td className="text-right">
                                            <button type="button" data-id="4222" data-message="Delete" className="btn btn-link btn-icon btn-clean shopifyEmbedDelete mx-1" onClick={()=>this.deleteGallery(item.id)}>
                                                <span className="svg-icon-col svg-icon-2x">
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                        <g id="Group_4611" data-name="Group 4611" transform="translate(10956 -4533)">
                                                            <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                                            <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                                                <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fillRule="evenodd">
                                                                </path>
                                                            </g>
                                                        </g>
                                                    </svg>
                                                </span>
                                            </button>
                                        </td>
                                    </tr> })
                                    :""}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
  }
}

const mapStateToProps = (state) => { 
    return {
        BigcommerceData: state.BigcommerceData.BigcommerceData,
        websiteList: state.websiteList.websiteList
    }
}
const mapDispatchToProps = dispatch => {
    return {
        PUBLISH_BIGCOMMERCE_GALLERY: (obj) => dispatch(PUBLISH_BIGCOMMERCE_GALLERY(obj)),
        DELETE_BIGCOMMERCE_PUBLISHED:(obj) => dispatch(DELETE_BIGCOMMERCE_PUBLISHED(obj))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(BigcommercePublish);