import { useEffect, useState } from "react";
import CodeMirror from "@uiw/react-codemirror";
import { tags as t } from '@lezer/highlight';
import { loadLanguage} from '@uiw/codemirror-extensions-langs';
import { createTheme } from '@uiw/codemirror-themes';
import Background from "./Background"; 
import { useDispatch, useSelector } from "react-redux";
import { DeleteKeys, updateCutomCSS } from "../../../../../../actions/appsMoreAction";
import ThemeSetting from "./ThemeSetting";
import UGCUpload from "./UGCUpload";
import CardStyle from "./CardStyle";
import { TOAST_DISMISS, TOAST_ERROR, TOAST_LOADING, TOAST_SUCCESS } from "../../../../../../utils";
import { ACTIVE_VIDEO_POPUP,APPS_MORE_BANNER_DETAIL, GET_CUSTOM_CSS_STATUS } from "../../../../../../reducer/keys"; 
import { SET_CUSTOM_CSS } from "../../../../../../reducer/themes/reducersKeys";
import GoogleAnalytics from "../Google-Analytic";
import ErrorFound from "../../../../../ErrorFound";
import CssStatus from "./elements/cssStatus";


const Banner = (props) => {
  const { openBanner, a_key, bannerDetail, appData, fonts, userData, onCtaChange, setBanner} = props
  const getCtastatus = useSelector((state) => state.appsMore);

  const [activeKey, setActiveKey] = useState('banner')
  const [customCSS, setCustomCSS] = useState(appData?.wall.Personalization.css)
  const [commerceTheme, setCommerceTheme] = useState(appData?.wall?.Personalization?.commerceTheme)
  const [loading, setLoading] = useState(false)
  const [ctaButton, setctaButton] = useState(appData?.wall?.Personalization?.ctaStatus)
  const [cssStatus, setcssStatus] = useState(appData?.wall?.Personalization?.cssStatus)
  const [ctaOn, setctaOn] = useState(false)
  const [cssOn, setcssOn] = useState(false) 
  const dispatch = useDispatch()

  let backgroundDetails = appData?.wall;
  let onsite = appData?.wall?.UgcSettings;
  let PersonalizationID = appData?.wall?.Personalization?.id
  let onsiteStatus = appData

  useEffect(() => {

    dispatch({ type: ACTIVE_VIDEO_POPUP, payload: 'banner' })

    window.$("#wallThemeRuleCtaStatus").on('change', function (e) {
      setctaOn(this.value)
       onCtaChange()
    })

    window.$("#wallcssStatus").on('change', function (e) { 
      setcssOn(this.value == 'true' ? true : false) 
    })
  }, [cssOn])

  


  const onCTAChange = () => {
  }

  const applyCss = () => {
    TOAST_LOADING()

    setLoading(true);

    updateCutomCSS(customCSS, PersonalizationID).then(res => {
      TOAST_DISMISS()
      const { data } = res;
      setLoading(false);
      if (data.status) {
        DeleteKeys()
        // TOAST_SUCCESS(data.message)
      }
      else TOAST_ERROR(data.message)
     // openBanner()
    }
    ).catch(err => {
      console.log('err', err)
      TOAST_ERROR('Error in updating')
    })
  }

  const onCTAClose = () => {
    setctaOn(false)
    //openBanner()
  }
  const onCSSClose = () => {
    setcssOn(false)
   // openBanner()
  }

  const onBackCSS = () => {
    setcssOn(false)
    setBanner()
    dispatch({ type: GET_CUSTOM_CSS_STATUS, payload: false })
  }



  const myTheme = createTheme({
    theme: 'light',
    settings: {
      background: '#f8f8f8',
      caret: 'none',
      gutterBackground: '#f7f7f7',
    },
    styles: [
      { tag: t.comment, color: '#b06213' },
      { tag: t.variableName, color: '#ff0000' },
      { tag: [t.string, t.special(t.brace)], color: '#b83b3b' },
      { tag: t.number, color: '#166948' },
      { tag: t.bool, color: '#92349f' },
      { tag: t.null, color: '#166948' },
      { tag: t.keyword, color: '#166948' },
      { tag: t.operator, color: '#166948' },
      { tag: t.className, color: '#ff0000' },
      { tag: t.definition(t.typeName), color: '#b83b3b' },
      { tag: t.typeName, color: '#b83b3b' },
      { tag: t.angleBracket, color: '#92349f' },
      { tag: t.tagName, color: '#4d9840' },
      { tag: t.attributeName, color: '#92349f' },
      { tag: t.color, color: '#3a2ba4' },
      { tag: t.color, color: '#3a2ba4' },
    ],
  });

  useEffect(() => {
    if (appData?.save_and_publish == true && appData?.is_custome_css_change == true) applyCss();
  }, [appData?.save_and_publish]);

 

  return (
    <div className="accordion customization_accordian" id="customizationAccordian">
    <div className="accordion-item">
        <h2 className="accordion-header mb-0" id="backgroundHeading">
            <button className="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#background" aria-expanded="true" aria-controls="background">
                Background
            </button>
        </h2>
        <div id="background" className="accordion-collapse collapse show" aria-labelledby="backgroundHeading" data-bs-parent="#customizationAccordian">
            <div className="accordion-body">
            <ErrorFound><Background backgroundDetails={backgroundDetails} save_and_publish={appData?.save_and_publish} is_backgroun_change={appData?.is_backgroun_change}  PersonalizationID={PersonalizationID} openBanner={openBanner} bannerDetail={appData.wall}/></ErrorFound>
            </div>
        </div>
    </div>

    <div className="accordion-item">
        <h2 className="card_style-header mb-0" id="cardStyleHeading">
            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#cardStyle" aria-expanded="false" aria-controls="cardStyle">
            Card Settings
            </button>
        </h2>
        <div id="cardStyle" className="accordion-collapse collapse" aria-labelledby="cardStyleHeading" data-bs-parent="#customizationAccordian">
            <div className="accordion-body">
            {fonts && fonts.length && appData?.wall && Object.keys(appData).length > 0 ? <ErrorFound><CardStyle onCtaChange={onCTAChange} save_and_publish={appData?.save_and_publish}  bannerDetail={appData?.wall} ownerId={appData?.wallOwner} closePopup={openBanner} setctaButton={setctaButton} fonts={fonts} planID={appData.planId} /></ErrorFound> : null}
            </div>
        </div>
    </div>

    <div className="accordion-item">
        <h2 className="accordion-header mb-0" id="themeSettingHeading">
            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#themeSetting" aria-expanded="false" aria-controls="themeSetting">
                More Actions
            </button>
        </h2>
        <div id="themeSetting" className="accordion-collapse collapse" aria-labelledby="themeSettingHeading" data-bs-parent="#customizationAccordian">
            <div className="accordion-body">
            <ErrorFound> <ThemeSetting planID={appData?.wall.UserDetail.planId} appData={appData} save_and_publish={appData?.save_and_publish}   is_theme_setting_change={appData?.is_theme_setting_change}  bannerDetail={appData?.wall} db_table={appData?.wall.UserDetail.db_table} adminId={appData.wall} userData={userData} closePopup={openBanner} customCssStatus={appData?.wall.Personalization.cssStatus} setcssStatus={setcssStatus}/></ErrorFound>
            </div>
        </div>
    </div>
    { (appData?.wall?.UserRule?.customCss!=0) ?
    <div className="accordion-item" defaultActiveKey={a_key ? a_key : activeKey}>
        <h2 className="custom_css-header mb-0" id="customCssHeading">
            <button className="accordion-button collapsed" id="customCss_btn" type="button" data-bs-toggle="collapse" data-bs-target="#customCss" aria-expanded="false" aria-controls="customCss">
                Custom CSS
            </button>
        </h2>
        <div id="customCss" className="accordion-collapse collapse" aria-labelledby="customCssHeading" data-bs-parent="#customizationAccordian">
            <div className="accordion-body">
              <CssStatus />
              <div className={`${appData?.wall?.Personalization?.cssStatus ? '' : 'inActiveCustomeCss'}`} >
            <ErrorFound> <CodeMirror value={customCSS} height="350px" save_and_publish={appData?.save_and_publish}  theme={myTheme} basicSetup={{ foldGutter: false, dropCursor: false, allowMultipleSelections: false, indentOnInput: false, autocompletion: false }} extensions={[loadLanguage('css')]} onChange={(e) => {setCustomCSS(e); dispatch({type: SET_CUSTOM_CSS, payload: e})}} /></ErrorFound>
            </div>
            </div>
        </div>
    </div>: null}



<div className="accordion-item">
        <h2 className="accordion-header mb-0" id="ongoogle_analytics">
            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#google_analytics" aria-expanded="false" aria-controls="google_analytics">
            Google Analytics
            </button>
        </h2>
        <div id="google_analytics" className="accordion-collapse collapse" aria-labelledby="ongoogle_analytics" data-bs-parent="#customizationAccordian">
            <div className="accordion-body">
            <ErrorFound><GoogleAnalytics getData={bannerDetail} save_and_publish={appData?.save_and_publish} is_utm_tracker_change={appData?.is_utm_tracker_change}/></ErrorFound>
            </div>
        </div>
    </div>
    
    { appData.onsite_status ?
    <div className="accordion-item">
        <h2 className="accordion-header mb-0" id="onSnapUp">
            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#SnapUp" aria-expanded="false" aria-controls="SnapUp">
            SnapUp
            </button>
        </h2>
        <div id="SnapUp" className="accordion-collapse collapse" aria-labelledby="onSnapUp" data-bs-parent="#customizationAccordian">
            <div className="accordion-body">
            <ErrorFound><UGCUpload ugcDetails={onsite} save_and_publish={appData?.save_and_publish}   is_magic_upload_change={appData?.is_magic_upload_change}   PersonalizationID={PersonalizationID} openBanner={openBanner} fonts={fonts} /></ErrorFound>
            </div>
        </div>
    </div>
    : ''}
  
</div>
   
  );
};




export default (Banner);