import { connect } from "react-redux";
import React, { Component } from 'react'
import ReactTooltip from "react-tooltip";
import { CARD_STYLE_INHERIT, IS_CARD_STYLE_CHANGE, SETTING_CUSTOM_CSS } from '../../../../../../../reducer/themes/reducersKeys';

 class CssStatus extends Component {
  render() {
    const {CssStatus} = this.props
    return (
        <div className="row align-items-center justify-content-between mb-4">
                <div className="col-7">
                  <label className="control-label mr-2 mb-0" htmlFor="webCssStatus"> Status 
                  <span className="svg-icon svg-icon-sm svg-icon-dark tooltips ms-2" data-tip="React-tooltip" data-for="toolTipCssStatus" >
                    <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                      <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                        <rect x="0" y="0" width="24" height="24"></rect>
                        <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10"></circle>
                        <rect fill="#000000" x="11" y="10" width="2" height="7" rx="1"></rect>
                        <rect fill="#000000" x="11" y="7" width="2" height="2" rx="1"></rect>
                      </g>
                    </svg>
                  </span>
                  <ReactTooltip multiline={true} id="toolTipCssStatus" place="top" type="dark" effect="solid">
                      Add your Custom CSS to personalize the look of your Gallery.
                  </ReactTooltip>
                  </label>
                </div>
                <div className="col-5 toggleOnBut justify-content-end">
                  <span className="switch justify-content-center">
                    <label htmlFor="webCssStatus">
                      <input type="checkbox"  id="webCssStatus" className="onoffswitch-checkbox updateStatus" defaultValue={CssStatus}  checked={CssStatus==1 ? true : false} onChange={(e) => { this.props.CARD_STYLE_INHERIT(Number(e.target.checked)) }} />
                      <span></span>
                    </label>
                  </span> 
                </div>
              </div>

 
    )
  }
}

const stateToProps = (state) => {
    return {
        CssStatus:state.appData?.wall?.Personalization?.cssStatus
    }
  }
  const DispatchToProps = dispatch => {
    return {
         CARD_STYLE_INHERIT: (status)=> {dispatch({type:SETTING_CUSTOM_CSS, payload:Number(status)}); },
    }
  }

export default connect(stateToProps, DispatchToProps)(CssStatus)