import React, { Component } from 'react'
import { WEBSITE_EMBED_URL } from '../../../../../actions/api';
export default class themeView extends Component {

    componentDidMount() {
        this.embedCode()
    }

    embedCode = () => {
        var isTagEmbd = !1, isTagShop = !0
            window.isTagShop=true;
            window.preview = true;
        var AppPath = `${WEBSITE_EMBED_URL}embed.json`;
        fetch(AppPath)
            .then(response => response.json())
            .then(json => {
                const { css, id, script } = json;
                let myCss = document.createElement("link");
                myCss.setAttribute("type", 'text/css');
                myCss.setAttribute("rel", 'stylesheet');
                myCss.setAttribute("href", css);
                document.head.appendChild(myCss);
                let myreact = document.createElement("script");
                myreact.setAttribute("src", script);
                document.body.appendChild(myreact);
            })
    }
    render() {
        const rootID = `root_taggShopRoot`
        return ( 
                <div className="tagshop" style={{ width: '100%', height: '100%', overflow: 'auto',background:'#fff' }}><div className="tagshop-socialwall" data-wall-id={localStorage.getItem("website_id")} data-source="website" data-is-load="0" data-render-id={rootID} id={`co_${rootID}`} >
                    <div id={rootID}></div>
                </div></div>
        )
    }
}