      setThankFontVarSelect(f);
      if (document
        .querySelector("#thnkVar-subtitle")) {
        document

          .querySelector("#thnkVar-subtitle")
        .innerHTML = (f[0].label.key);

        document.getElementById('thnkVar-subtitle').style.fontFamily = `${e.label.key}`
        document.getElementById('thnkVar-subtitle').className = `sGFfonte-${f[0].label.key}`
      }
