import React, { PureComponent } from "react"; 
import { Link } from "react-router-dom";
export default class Error207 extends PureComponent {
  render() {    
    const {wall}= this.props; 
    return (
      <div className="ts_error_content">
          <div className="ts_error_title">No Posts!</div>
          <div className="ts_error_des">It seems your all posts are private. Please go to  You can <Link style={{ cursor: 'pointer' }} to={`/content/moderation/${localStorage.getItem('wallId')}`} className="manage-feed"> Moderation </Link>  and make some posts public.</div>
         
      </div>
    );
  }
}
