import React, { Component } from 'react'
import ReactTooltip from "react-tooltip";

export default class toggleBtn extends Component {
    render() {
    const { toggleValue, callbackFn, tooltipTxt , toggleName, callbackFn2, dispatchfn} =  this.props

        return (
            <div className="d-flex align-items-center justify-content-between">
                <div>
                  <label className="control-label mr-2 mb-0" htmlFor={`wall${toggleName.replace(" ","")}`}>
                    {toggleName}
                  </label>
                  <span className="svg-icon svg-icon-sm svg-icon-dark tooltips" data-tip="React-tooltip" data-for={`toolTip${toggleName.replace(" ","")}`} >
                    <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                      <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                        <rect x="0" y="0" width="24" height="24"></rect>
                        <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10"></circle>
                        <rect fill="#000000" x="11" y="10" width="2" height="7" rx="1"></rect>
                        <rect fill="#000000" x="11" y="7" width="2" height="2" rx="1"></rect>
                      </g>
                    </svg>
                  </span>
                  <ReactTooltip multiline={true} id={`toolTip${toggleName.replace(" ","")}`} place="top" type="dark" effect="solid">
                      {tooltipTxt}
                  </ReactTooltip>
                </div>
                <div className="toggleOnBut">
                  <span className="switch justify-content-center">
                    <label htmlFor={`wall${toggleName.replace(" ","")}`}>
                      <input type="checkbox"  id={`wall${toggleName.replace(" ","")}`} className="onoffswitch-checkbox updateStatus" defaultValue={toggleValue} checked={toggleValue==1 ? true : false} onChange={(e) => {
                      callbackFn(e.target.checked)
                      dispatchfn(e.target.checked)
                      callbackFn2(e.target.checked ? false: e.target.checked)
                      
                     }}
                      />
                      <span></span>
                    </label>
                  </span>{" "}
                </div>
              </div>
        )
    }
}
