import React, { useState, useEffect, useRef } from 'react';
import Modal from 'react-bootstrap/Modal';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';
import { useForm } from 'react-hook-form';
import { googleAnaytic } from '../../../../../actions/appsMoreAction';
import { TOAST_ERROR, TOAST_SUCCESS, TOAST_LOADING, TOAST_DISMISS } from "../../../../../utils";
import { GET_GOOGLE_TRACKING_ID } from "../../../../../reducer/keys";
import { useSelector, useDispatch } from 'react-redux';
import { ToastContainer } from 'react-toastify';
import { HOST_URL } from '../../../../../actions/api';
import { IS_UTM_TRACKER_CHANGE } from '../../../../../reducer/themes/reducersKeys';
import { UPDATE_UTM_TRACKER } from '../../../../../actions/websiteAction';

const GoogleAnalytics = ({ getData, save_and_publish, is_utm_tracker_change  }) => {


    const {Wall} = useSelector((state) => state.appData?.wall);
    const dispatch = useDispatch();  

    const [googleTackingCode, setgoogleTackingCode] = useState(Wall?.google_tacking_code);
    const [googleTackingStatus, setgoogleTackingStatus] = useState(Wall?.google_tacking_status);
    const [UtmTackingStatus, setUtmTackingStatus] = useState(Wall?.utm_status);
    
    const [utmSource, setUtmSource] = useState(Wall?.utm_source)
    const [utmMedium, setUtmMedium] = useState(Wall?.utm_medium)
    const [utmCampaign, setUtmCampaign] = useState(Wall?.utm_campaign)
    

    const [trackingIdError, setTrackingIdError] = useState(false) 
    const [utmSourceError, setUtmSourceError] = useState(false) 
    const [utmMediumError, setUtmMediumError] = useState(false) 
    const [utmCampaignError, setUtmCampaignError] = useState(false) 


 
    const [spinner, setspinner] = useState(false);


    const applyUTM_setting = () => {
        if (googleTackingCode.trim().length == 0 && googleTackingStatus == 1) { setTrackingIdError(true); return false }
        if (UtmTackingStatus == 1) {
            if (utmSource.trim().length == 0) { setUtmSourceError(true); return false }
            else if (utmMedium.trim().length == 0) { setUtmMediumError(true); return false } 
            else if (utmCampaign.trim().length == 0) {setUtmCampaignError(true); return false }
            else{}
         } 
 
     dispatch(UPDATE_UTM_TRACKER({googleTackingCode, googleTackingStatus, UtmTackingStatus, utmSource, utmCampaign, utmMedium}))
   
    }

   const changeUtmSource=(value)=>{
    let result = value?.replace(/ /g, '-').replace(/[^\w-]+/g, '');
    setUtmSource(result)
   }
   const changeUtmMedium=(value)=>{
    let result = value?.replace(/ /g, '-').replace(/[^\w-]+/g, '');
    setUtmMedium(result)
   }
   const changeUtmCampaign=(value)=>{
    let result = value?.replace(/ /g, '-').replace(/[^\w-]+/g, '');
    setUtmCampaign(result)
   }


    useEffect(() => {
   if(save_and_publish==true && is_utm_tracker_change==true) applyUTM_setting()
    }, [save_and_publish]);

    return (
        <> <div className="google_analytics_wrap">
            <div className="pt-5 mb-5 d-flex align-items-center justify-content-between">
                <div>
                    <label htmlFor="googleAnalyticsStatus">
                        Google Analytics
                        <OverlayTrigger overlay={<Tooltip>Allow Google Analytics tracking for this website.</Tooltip>}>
                            <span className="svg-icon svg-icon-sm svg-icon-dark info-detail" data-toggle="tooltip" data-theme="dark" data-bs-original-title="">
                                <span> <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1"> <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd"> <rect x="0" y="0" width="24" height="24"></rect> <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10"></circle> <rect fill="#000000" x="11" y="10" width="2" height="7" rx="1"></rect> <rect fill="#000000" x="11" y="7" width="2" height="2" rx="1"></rect> </g> </svg> </span>
                            </span>
                        </OverlayTrigger>
                    </label>
                </div>
                <div className="toggleOnBut">
                    <div className="onoffswitch">
                        <span className="switch justify-content-center">
                            <label htmlFor="profanityStatusnew">
                                <input type="checkbox" name="google_tacking_status" id="profanityStatusnew" className="onoffswitch-checkbox" data-off-color="#989898" data-on-color="#009385" defaultChecked={googleTackingStatus} onChange={(e) => { setgoogleTackingStatus(e.target.checked); setTrackingIdError(false); dispatch({type:IS_UTM_TRACKER_CHANGE, payload:true})}} />
                                <span></span>
                            </label>
                        </span>
                    </div>
                </div>
            </div>

            <div className="mb-10 utm_tracker_wrap">
                <label >Tracking ID</label>
                <input placeholder="e.g. UA-123456-1 A"  className={`form-control input-radius ${googleTackingStatus ? 'active' :''}`}  defaultValue={googleTackingCode} maxLength="100" type="text" onChange={(e) => {setgoogleTackingCode(e.target.value); setTrackingIdError(false); dispatch({type:IS_UTM_TRACKER_CHANGE, payload:true})}} />
                {trackingIdError ? <span className="errorMessage"> Please Fill Tracking ID </span>:''}
            </div>
           

            <div className="border-top mt-10 utm_tracker_wrap">
            <div className="pt-5 mb-5 mt-5 d-flex align-items-center justify-content-between">
                <div>
                    <label htmlFor="googleAnalyticsStatus">
                        UTM  Tracker
                        <OverlayTrigger overlay={<Tooltip>Allow UTM tracking for this website.</Tooltip>}>
                            <span className="svg-icon svg-icon-sm svg-icon-dark info-detail" data-toggle="tooltip" data-theme="dark" data-bs-original-title="">
                                <span> <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1"> <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd"> <rect x="0" y="0" width="24" height="24"></rect> <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10"></circle> <rect fill="#000000" x="11" y="10" width="2" height="7" rx="1"></rect> <rect fill="#000000" x="11" y="7" width="2" height="2" rx="1"></rect> </g> </svg> </span>
                            </span>
                        </OverlayTrigger>
                    </label>
                </div>
                <div className="toggleOnBut">
                    <div className="onoffswitch">
                        <span className="switch justify-content-center">
                            <label htmlFor="utmStatusnew">
                                <input type="checkbox" name="utm_tacking_status"  id="utmStatusnew"  className="onoffswitch-checkbox" data-off-color="#989898" data-on-color="#009385" defaultChecked={UtmTackingStatus} onChange={(e) => {setUtmTackingStatus(e.target.checked);  dispatch({type:IS_UTM_TRACKER_CHANGE, payload:true}) ; setUtmSourceError(false); setUtmMediumError(false); setUtmCampaignError(false);}} />
                                <span></span>
                            </label>
                        </span>
                    </div>
                </div>
            </div>
                <div className="row">
                    <div className="col-sm-12 mb-5">
                        <label>UTM Source</label>
                        <input type="text" placeholder="shop-galley" className={`form-control input-radius ${UtmTackingStatus ? 'active' :''}`}  value={utmSource} onChange={(e) => { changeUtmSource(e.target.value); setUtmSourceError(false); dispatch({type:IS_UTM_TRACKER_CHANGE , payload:true}) }} />
                        {utmSourceError ? <span  className="errorMessage"> Please Fill Tracking ID </span>:''}
                    </div>

                    <div className="col-sm-12 mb-5">
                        <label>UTM Medium</label>
                        <input type="text" placeholder="shop-buttom" className={`form-control input-radius ${UtmTackingStatus ? 'active' :''}`}  value={utmMedium} onChange={(e) => { changeUtmMedium(e.target.value); setUtmMediumError(false); dispatch({type:IS_UTM_TRACKER_CHANGE, payload: true}) }}/>
                        {utmMediumError ? <span  className="errorMessage" > Please Fill Tracking ID </span>:''}
                    </div>

                    <div className="col-sm-12 mb-5">
                        <label>UTM Campaign</label>
                        <input type="text" placeholder="Tagshop" className={`form-control input-radius ${UtmTackingStatus ? 'active' :''}`}  value={utmCampaign} onChange={(e) => { changeUtmCampaign(e.target.value); setUtmCampaignError(false);  dispatch({type:IS_UTM_TRACKER_CHANGE, payload:true}) }}/>
                        {utmCampaignError ? <span  className="errorMessage" > Please Fill Tracking ID </span>:''}
                    </div>
                </div>
            </div>
        </div>
            {/* <button type="submit" className={`btn btn-primary gAsave ${spinner ? `spinner spinner-white spinner-right disabled` : null}`} onClick={()=>applyUTM_setting()}>Apply</button> */}
        </>
    );
};

export default GoogleAnalytics;
