import { Modal } from "react-bootstrap"
import { CLOUDPATH_TAGGSHOP, HOST_URL } from "../../../../../../actions/api"
import $ from 'jquery';
import { useState } from "react";
import { useDispatch } from "react-redux";
import { togglePriceSection } from "../../../../../../actions/show_modal_Action";

const CssOnOffUpgrad = ({open, onHide, onBack}) => {
   
    const [cssUpload , setCssUpload]  =useState (null);
const dispatch = useDispatch()

    return(
        <Modal
        size="lg"
        dialogClassName=" modal-dialog-centered modal-dialog"
        show={true}
        onHide={onHide}
      >
<Modal.Header className="modal-header justify-content-end px-5 border-0 pb-0">
          <button
            type="button"
            className="btn-close"
            data-bs-dismiss="modal"
            onClick={onHide}
          >
          </button>
        </Modal.Header>
<Modal.Body className="body-bg">
                <div className="row py-5">
                    <div className="col-lg-12">
                        <div className="upgredeMain text-center">	
                            <div className="Errorimage pb-3"><img  className="img-fluid" style={{width:195, height:195}} src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/error/problem-error.svg`} /></div>
                            <div className="permissionMsg mt-4 px-4">
                                <h4 className="Errorimage mb-5 mb-5">Unable to use Custom CSS?</h4>
                                <p className="msgSec">Oops! Looks like you need to upgrade your plan in order to use Custom CSS</p>
                            </div>
                            <div className="buttonUpgred mt-10 d-flex align-items-center justify-content-center w-100">
                                <a  id="addonFeaturesScroll" onClick={()=>{dispatch(togglePriceSection(true, 2));onHide()}}  className="commanBtn btn btn-primary px-5 d-inline-block" data-factors-click-bind="true">
                                    Upgrade Now
                                </a>
                            </div>
                            {/* <div className="d-flex align-items-center justify-content-center w-100">
                                <a  id="addonFeaturesScroll" onClick={() => onBack()}  className="btn btn-link px-4 py-1 backToPer" data-factors-click-bind="true">
                                    Back
                                </a>
                            </div> */}
                        </div>
                    </div>
                </div>
  </Modal.Body>
  </Modal>
    )
}

export default CssOnOffUpgrad;

