import React, { Component } from 'react'
import { autoCompleteTag, tagsEmbedSave, ADD_SELECTED_TAGS, REMOVE_SELECTED_TAGS } from '../../../../../../actions/moderationActions'
import { connect } from 'react-redux';
import { EMBED_URL  } from '../../../../../../actions/api'
import { TOAST_ERROR, TOAST_SUCCESS } from '../../../../../../utils';
import Preview from './preview'; 
class tagbasedEmbed extends Component {
    state = { width: 100, height: 100, widthPr: '%', heightPr: '%', searchData: [], isLoading: false, searchPost: null, openDropdwn:false, isPreview:false, dataTag:false, copied:false}

    copyText = (e) => {
        var copyText = document.getElementById('wallembedareaLiteCode03').value
        const textarea = document.createElement('textarea');
        textarea.value = copyText;
        textarea.style.visibility = 'none';
        document.body.appendChild(textarea);
        textarea.select();
        document.execCommand('copy');
        document.body.removeChild(textarea);
        this.setState({copied:true})
        setTimeout(()=>{this.setState({copied:false})},1000)

    }

    onLoad = (e) => {
        var text = e.target.value;
        if (text.length > 0) {
            this.setState({ isLoading: true , openDropdwn:true})
            autoCompleteTag(text).then((response) => {
                const { tags, status } = response.data;
                if (status) this.setState({ searchData: tags, isLoading: false })
                else this.setState({ searchData: [], isLoading: true })

            })
        }
        else {
            this.setState({ searchData: [], isLoading: false, openDropdwn:false })
        }
    }
    componentDidMount(){if(this.props.allTags.length > 0) this.setState({dataTag:true});}

    onClickToAddTags = (tag) => {
        const { allTags} = this.props;
        if(!allTags.includes(tag))this.props.ADD_SELECTED_TAGS(tag);
        this.setState({openDropdwn:false})
    };
    removeTags = (tag) => {this.props.REMOVE_SELECTED_TAGS(tag);  setTimeout(()=>{this.onTagsEmbedSave(3)},500) }

    onTagsEmbedSave=(id)=>{
        const { allTags} = this.props;
        if(id==1){
            this.setState({isPreview:false})
            if(allTags.length > 0) this.setState({dataTag:true});
            TOAST_SUCCESS('Embed code generated successfully.')
        }
        if(id==2){
            if(allTags.length > 0) this.setState({isPreview:true,dataTag:true});
            else TOAST_ERROR("Please select tag.")
        }
        if(id==3){
            if(!(allTags.length > 0)) this.setState({isPreview:false,dataTag:false});
        } 
        tagsEmbedSave(allTags).then((response) => {
            const { tags, status } = response.data;
            if (status) this.setState({ searchData: tags, isLoading: false })
            else this.setState({ searchData: [], isLoading: true })
        })
    }



    render() {
        const { width, height, widthPr, heightPr, searchData, openDropdwn , isPreview, dataTag, copied} = this.state 
        const { allTags} = this.props;
        return (<>
            <div className="form-group mb-6 mb-lg-10 following_steps">
                <h6 className="mb-5 text-dark">1. Select your tags</h6>
                <div className="row">
                    <div className="col-12">
                        <div className="noLoader maintainPopUp embed_tags" id="">
                            <div className="form-group">
                                <div className="input-icon input-icon-right spinner-right formtagarea">
                                    <input type="text" className="form-control postTagFilter" placeholder="Search..." onChange={(e => this.onLoad(e))} />
                                    <span><i className="fa fa-search icon-md"></i></span>
                                    <div className="autoSuggestionList postTagsSugg">

                                        {
                                            (openDropdwn && searchData && searchData.length > 0) ? <div className="dropdown-menu-list">
                                                <ul className="list-unstyled navi navi-hover">
                                                    {
                                                        searchData.map((sItem, indexItem) => <li key={indexItem} tags={sItem} data-id={sItem} className="image-tags navi-item" onClick={() => this.onClickToAddTags(sItem)}>
                                                            <a className="navi-link">{sItem}</a>
                                                        </li>)
                                                    }
                                                </ul>
                                            </div> : null
                                        }


                                    </div>
                                </div>
                            </div>
                            <div className="tags-list mt-5">
                                <ul>
                                    {allTags && allTags.length > 0 ? allTags.map((item, i) => {
                                        return <li key={`tg_${i}`}>
                                            <span>{item}</span>
                                            <button className="removeSelectedTag" onClick={() => this.removeTags(item)}>
                                                <i className="ki ki-close"></i>
                                            </button>
                                        </li>
                                    }) : ''}

                                </ul>
                            </div>
                            <div className="form-actions mt-8">
                                <div className="d-flex align-items-center justify-content-start w-100">
                                    <button type="button" id="GenerateBtn" className="embedTagsGenerate commanBtn btn-md btn btn-primary mr-3" onClick={()=>this.onTagsEmbedSave(1)}>Generate Code</button>
                                    <button type="button" id="PreviewBtn" className="PreviewTagsWallBtn commanBtn btn-md btn btn-outline-dark" onClick={()=>this.onTagsEmbedSave(2)}>Preview</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="symbol symbol-lg-75 symbol-circle symbol-primary d-none">
                        <span className="font-size-h3 font-weight-boldest">LM</span>
                    </div>
                </div>
            </div>
            {isPreview ? <Preview dataTag={allTags && allTags.length && dataTag ? allTags.join(","): ""} isTags={true}/>:''}
            
            <div className="form-group mb-6 mb-lg-10 following_steps GenerateTags18 GenerateTagsWall">
                <h6 className="mb-5 text-dark">2. Adjust the width and height of the gallery as per the container size of your product page template</h6>
                <div className="row">
                    <div className="col-md-6">
                        <div className="form-group mb-2">
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text">Width</span>
                                </div>
                                <input type="number" id="getWidth" className="form-control form-control-lg getWidth" max={widthPr == '%' ? "100" : ''} min={0} value={width} onChange={(e) => this.setState({ width: e.target.value })} />
                                <div className="input-group-append">
                                    <select className="kt-selectpicker form-control form-control-lg" id="embed-width02" onChange={(e) => this.setState({ widthPr: e.target.value })}>
                                        <option value="%" selected={widthPr == '%' ? true : false} >%</option>
                                        <option value="px" selected={widthPr == 'px' ? true : false} >px</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="form-group mb-2">
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text">Height</span>
                                </div>
                                <input type="number" id="getHeight02" className="form-control form-control-lg getHeight" value={height} max={heightPr == '%' ? "100" : ''} min={0} onChange={(e) => this.setState({ height: e.target.value })} />
                                <div className="input-group-append">
                                    <select className="kt-selectpicker form-control form-control-lg" id="embed-height02" onChange={(e) => this.setState({ heightPr: e.target.value })}>
                                        <option value="%" selected={heightPr == '%' ? true : false} >%</option>
                                        <option value="px" selected={heightPr == 'px' ? true : false} >px</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {this.props.source == 'HTML' ?
                <div className="form-group mb-6 mb-lg-10 following_steps GenerateTags16 GenerateTagsWall">
                    <h6 className="mb-5 text-dark">3. Add your Product ID identifier variable after the Data Filter ID section in the publish code in Step 3</h6>
                    <p>For Example: data-filter-id="<span className="text-dark">{('{{product-id}}')}</span></p>
                </div> : ''}

            <div className="form-group mb-6 mb-lg-10 following_steps">
                {this.props.source == 'HTML' ?
                    <h6 className="mb-5 text-dark">4. Now copy the publish code and paste it into product page template of your HTML website where you want to show the gallery</h6> :
                    <h6 className="mb-5 text-dark">3. Copy the below given publish code for your product page galleries</h6>}
                <div className="position-relative mb-0 embed_code embed_code_box light_embcode_box pb-6">
                    <textarea readOnly="readonly" id="wallembedareaLiteCode03" className="form-control bg-white form-control-solid copyme" style={{ height: "2.22222px" }} onClick={(e)=>this.copyText(e)}
                        value={(`<div class="tagshop" style="width:${width}${widthPr};height:${height}${heightPr};overflow: auto;"><div class="tagshop-socialwall" data-wall-id="${localStorage.getItem('website_id')}"  data-tags="${dataTag ? 'true' : 'false'}" data-keywords="${allTags && allTags.length && dataTag ? allTags.join(","): ""}" data-source="website" ></div><script src="${EMBED_URL}" type="text/javascript"></script></div>`)}>
                    </textarea>
                   {copied ? <span id="copy-success-alert" style={{display: "block"}}>Copied!</span>:''}
                    <div className="emebed_action">
                        <button className="btn btn-text p-0 mr-2 copy-btn" data-toggle="tooltip" data-theme="dark" title="Copy Code" onClick={(e)=>this.copyText(e)}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="18.17" height="18.17" viewBox="0 0 18.17 18.17">
                                <g id="Group_7224" data-name="Group 7224" transform="translate(-32 -32)">
                                    <path id="Path_7006" data-name="Path 7006" d="M124.005,112H114.92a2.92,2.92,0,0,0-2.92,2.92v9.085a2.92,2.92,0,0,0,2.92,2.92h9.085a2.92,2.92,0,0,0,2.92-2.92V114.92A2.92,2.92,0,0,0,124.005,112Z" transform="translate(-76.755 -80)" fill="#c7c7c7"></path>
                                    <path id="Path_7007" data-name="Path 7007" d="M37.191,44.811h9.567a2.925,2.925,0,0,1-2.753,1.947H34.92A2.92,2.92,0,0,1,32,43.838V34.753A2.925,2.925,0,0,1,33.947,32v9.567A3.245,3.245,0,0,0,37.191,44.811Z" transform="translate(0 3.412)" fill="#c7c7c7"></path>
                                </g>
                            </svg>
                        </button>
                    </div>
                </div>
                {this.props.showNote == 1 ?
                    <p><span className="font-weight-boldest">Note:</span> Make sure that the Product ID key is correct which is mentioned after the Data-Filter-ID in code.</p> : ''}

            </div>




        </>
        )
    }
}



const mapStateToProps = state => {
    const {allTags} = state 
    return {
        allTags: allTags.selectedTags
    }
}
const mapDispatchToProps = dispatch => {
    return {
        ADD_SELECTED_TAGS: (tag) => dispatch(ADD_SELECTED_TAGS(tag)),
        REMOVE_SELECTED_TAGS: (tag) => dispatch(REMOVE_SELECTED_TAGS(tag)),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(tagbasedEmbed)