                      <div className="col-6 toggleOnBut justify-content-end"> <span className="switch justify-content-center"> <label htmlFor="wallsocialAction">  
                      <input type="checkbox" id="wallsocialAction" className="onoffswitch-checkbox updateStatus" defaultValue={socialAction} checked={socialAction} onChange={(e) => {this.setState({ socialAction: e.target.checked }); this.props.CARD_STYLE_SHARE_ACTION(Number(e.target.checked))}} /> <span></span> </label> </span> </div>
