import { useRef, useState } from "react";
import Select from "react-select";
import Slider from "rc-slider";
import { useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { DeleteKeys, updateThemeSetting } from "../../../../../../actions/appsMoreAction";
import { TOAST_DISMISS, TOAST_ERROR, TOAST_LOADING, TOAST_SUCCESS, COLUMNLIST , MOBILE_COLUMNLIST} from "../../../../../../utils";
import ReactTooltip from "react-tooltip";
import DateTimeField from '@1stquad/react-bootstrap-datetimepicker';
import '@1stquad/react-bootstrap-datetimepicker/css/bootstrap-datetimepicker.css';
import { GET_CUSTOM_CSS_STATUS } from "../../../../../../reducer/keys";
import moment from 'moment';
import { HOST_URL } from "../../../../../../actions/api";
import ToggleBtn from "./elements/toggleBtn"
import { CLOSE_POP_UP, IS_THEME_SETTING_CHANGE, SETTING_AUTO_SLIDER,SETTING_VIDEO_PLAY, SETTING_BEST_FIT, SETTING_CAROUSEL_POST, SETTING_COLUMNS, SETTING_COLUMNS_MOBILE, SETTING_CUSTOM_CSS, SETTING_FEATURE_POPUP, SETTING_MINIMUM_POSTWIDTH, SETTING_MOBILE_POPUP, SETTING_NO_OF_ROW, SETTING_POPUP_SLIDER, SETTING_POST_NUMBER, SETTING_POST_SPACING, SETTING_SHOW_MORE_BTN, SETTING_TIME_PER_SLIDER, SETTING_TRIM_CONTENT } from "../../../../../../reducer/themes/reducersKeys";
import CssOnOffUpgrad from "./CssOnOffUpgrad";
import { GET_ALL_WEBSITE_LIST } from "../../../../../../actions/websiteAction";
import { CHANGE_WEBSITE_WALL } from "../../../../../../actions/themeActions/themeActions";


const ThemeSetting = (props) => {
  const [commerceTheme, setCommerceTheme] = useState(props.bannerDetail.Personalization.commerceTheme)
  const [autoplayVideo, setAutoplayVideo] = useState()
  const [carouselPost, setCarouselPost] = useState()
  const [customCSS, setCustomCSS] = useState()
  const [shufflePost, setShufflePost] = useState(false)
  const [loadMore, setLoadMore] = useState()
  const [autoLoad, setAutoLoad] = useState()
  const [postText, setPostText] = useState()
  const [mobilePopup, setMobilePopup] = useState()
  const [filterStatus, setFilterStatus] = useState()
  const [trimContent, setTrimContent] = useState()
  const [excludeRetweets, setExcludeRetweets] = useState()
  const [popupSlider, setPopupSlider] = useState()
  const [bestFit, setBestFit] = useState()
  const [textDecoration, setTextDecoration] = useState()
  const [autoSlide, setAutoSlide] = useState()
  const [videoPlay, setVideoPlay] = useState()
  const [autoPlay, setAutoPlay] = useState()
  const [hideOld, setHideOld] = useState()
  const [totalPost, setTotalPost] = useState('')
  const [spinner, setspinner] = useState(false);
  const [wallSlideDuration, setWallSlideDuration] = useState()
  const [postWidth, setPostWidth] = useState()
  const [wallPadding, setWallPadding] = useState()
  const [customUrlerror, setCustomUrlerror] = useState(false)
  const [postFeatured, setPostFeatured] = useState()

  const [numberOfCoumn, setNumberOfCoumn] = useState()
  const [mobileColumn, setMobileColumn] = useState()
  const [multiRow, setMultiRow] = useState()

  const [extra, setExtra] = useState()

  const dispatch = useDispatch()

  const [columnSelect, setColumnSelect] = useState(COLUMNLIST(commerceTheme));
  const [moblieColumnSelect, setMoblieColumnSelect] = useState(MOBILE_COLUMNLIST(commerceTheme));

  //const [defaultCoumns, setDefaultCoumns] = useState()

  const [rowSelect, setRowSelect] = useState([
    { label: "1 Row", value: 1 },
    { label: "2 Rows", value: 2 },
  ]);

  const [date, setDate] = useState(props.bannerDetail.Personalization.showHideOldPost)
  const myRefname = useRef(null);
  const [customUrl, setCustomUrl] = useState()
  const [showHideOldPost, setShowHideOldPost] = useState()
  const [showHideOlderPost, setShowHideOlderPost] = useState()
  const [customSlelect, setCustomSlelect] = useState([
    { label: "One Day Ago", value: 1 },
    { label: "Two Days Ago", value: 2 },
    { label: "Three Days Ago", value: 3 },
    { label: "Four Days Ago", value: 4 },
    { label: "Five Days Ago", value: 5 },
    { label: "Six Days Ago", value: 6 },
    { label: "Seven Days Ago", value: 7 },
    { label: "One Month", value: 30 },
    { label: "Custom Select", value: 8 },
  ]);
  const [cssUpgrade, setCssUpgrade] = useState(false)

  var d = new Date(0); // The 0 there is the key, which sets the date to the epoch
  d.setUTCSeconds(date == 0 ? 1641015000 : date);
  var minimalDate = moment(d).format("MM DD YYYY hh:mm");
  let defaultDate = moment('01.01.2022', 'DD.MM.YYYY')

  const handleChange = e => {
    var myDate = new Date(e); // Your timezone!
    var myEpoch = myDate.getTime() / 1000
    setDate(myEpoch)
    setShowHideOldPost(moment(e).format("MM/DD/YYYY hh:mm"))
  };

  const onChangeCustomCSS = value =>{ 
    if(props.appData?.wall?.UserRule?.customCss == 0) {
      setCssUpgrade(true)
    }
    else{
      dispatch({ type: GET_CUSTOM_CSS_STATUS, payload: value }); 
      setCustomCSS(value)
      setTimeout(() => {
        const customCss = document.querySelector("#customCss_btn")
        if(customCss && value){
          customCss.click()
        }
      }, 100);
    }
  }
  const onChangeOldPost = value =>{
    setHideOld(Number(value))
    if(!Number(value)) return setShowHideOlderPost(0)
     setShowHideOlderPost(1)
  }

  useEffect(() => {
    setAutoplayVideo(props.bannerDetail.Personalization?.autoplayVideo ? props.bannerDetail.Personalization?.autoplayVideo : true);
    setCarouselPost(props.bannerDetail.Personalization?.carouselPost);
    setCustomCSS(props.bannerDetail.Personalization?.cssStatus);
    // setShufflePost(props.bannerDetail.Personalization?.randomizePost);
    setLoadMore(props.bannerDetail.Personalization?.loadMoreStatus);
    setAutoLoad(props.bannerDetail.Personalization?.autoScrollStatus);
    setPostText(props.bannerDetail.Personalization?.postText);
    setMobilePopup(props.bannerDetail.Personalization?.mobilePopup);
    setFilterStatus(props.bannerDetail.Personalization?.filterStatus);
    setTrimContent(props.bannerDetail.Personalization?.trimcontent);
    setExcludeRetweets(props.bannerDetail.Personalization?.retweetexclude ? props.bannerDetail.Personalization?.retweetexclude :0);
    setPopupSlider(props.bannerDetail.Personalization?.popupSlideShow);
    setBestFit(props.bannerDetail.Personalization?.fitRow);
    setTextDecoration(props.bannerDetail.Personalization?.textDecorate);
    setAutoSlide(props.bannerDetail.ThemeRule.slidePost);
    setVideoPlay(props.bannerDetail.Personalization?.videoPlay);
    setAutoPlay(props.bannerDetail.ThemeRule.autoPlay);
    setHideOld(props.bannerDetail.Personalization?.showHideOlderPost == 0 ? 0 : 1);

    setTotalPost(props.bannerDetail.ThemeRule.numberOfPosts ? props.bannerDetail.ThemeRule.numberOfPosts : 10);
    setCustomUrl(props.bannerDetail.Personalization?.customUrl);
    setPostFeatured(`${props.bannerDetail.Personalization?.postFeatured}`);
    setNumberOfCoumn(`${props.bannerDetail.ThemeRule.numberOfCoumn}`);
    setMobileColumn(`${props.bannerDetail.ThemeRule.mobileColumn}`);
    setMultiRow(props.bannerDetail.ThemeRule.multiRow);
    setShowHideOlderPost(`${props.bannerDetail.Personalization?.showHideOlderPost}`);
    setShowHideOldPost(props.bannerDetail.Personalization?.showHideOldPost)

    setWallSlideDuration(props.bannerDetail.ThemeRule.slideDuration);
    setPostWidth(props.bannerDetail.Personalization?.minimumPostWidth);
    setWallPadding(props.bannerDetail.Personalization?.padding);
    setColumnSelect(COLUMNLIST(commerceTheme))
    setMoblieColumnSelect(MOBILE_COLUMNLIST(commerceTheme))
    //setDefaultCoumns(columnSelect ? columnSelect.filter(itm => itm.value == numberOfCoumn)[0]:'')
  }, [])

  const {userData} = useSelector(state => state.authorized);
  // var t = new Date();
  // t.setSeconds(1664669);
  // var formatted = moment(t).format('DD-MM-YYYY HH:mm');
  useEffect(() => { 
    if(props.save_and_publish==true && props.is_theme_setting_change==true)  submitData(); 
  }, [props.save_and_publish]);

  const submitData = () => {
    let data = {
      websiteId:localStorage.getItem('website_id'),
      wallId: localStorage.getItem('active_wall'),
      adminId: userData?.user_data?.user.id,
      ownerId: userData?.user_data?.owner,
      db_table: props.db_table,
      p_id: props.bannerDetail.Personalization.id,
      autoplayVideo:  Number(autoplayVideo),
      carouselPost:  Number(carouselPost),
      // oldcarouselPost: 1,
      cssStatus:  props.appData?.wall?.Personalization?.cssStatus,
      randomizePost:  Number(shufflePost),
      loadMoreStatus:  Number(loadMore),
      autoScrollStatus:  Number(autoLoad),
      postText:  Number(postText),
      mobilePopup:  Number(mobilePopup),
      //filterStatus: filterStatus,
      // oldfilterStatus: 1,
      trimcontent:  Number(trimContent),
      // oldtrimcontent: 0,
      // retweetexclude:  Number(excludeRetweets),
      popupSlideShow:  Number(popupSlider),
      // oldpopupSlideShow: 1,
      textDecorate:  Number(textDecoration),
      // oldtextDecorate: 0,
      postFeatured:  Number(postFeatured),
      // oldpostFeatured: 0,
      customUrl: `${customUrl}`,
      numberOfPosts: totalPost,
      hidePosts: showHideOlderPost == 0 ? 0 : 1,
      showHideOlderPost: showHideOlderPost,
      showHideOldPost: showHideOldPost,
      minimumPostWidth: postWidth,
      padding: wallPadding,
      theme_id: props.bannerDetail.ThemeRule.themeId,
      // animateBackground: 0,
      numberOfCoumn: numberOfCoumn,
      mobileColumn: mobileColumn,
      multiRow: multiRow,
      fitRow: Number(bestFit),
      autoPlay: autoPlay,
      slidePost: autoSlide,
      videoPlay:videoPlay,
      slideDuration: wallSlideDuration
    }

    if (postFeatured == 3 && validateUrl(customUrl) || postFeatured != 3) {
      setspinner(true)
      updateThemeSetting(data).then(res => {
        const { data } = res
        setspinner(false)
        if (data.status) {
        //  DeleteKeys();
        //  TOAST_DISMISS()
        dispatch(GET_ALL_WEBSITE_LIST())
        // dispatch(CHANGE_WEBSITE_WALL(localStorage.getItem('active_wall'), totalPost))
        //  TOAST_SUCCESS(`Theme settings saved successfully.`);

        } else {
     //     TOAST_DISMISS()
     //     TOAST_ERROR(data.message);
      //    props.closePopup()
        }
      }).catch((error) => console.warn(error))
    } else {
      setCustomUrlerror(true)
    }
  }
  
  const callbackFn2Console=()=>{ console.log("") }




  const validateUrl = (value) => {
    return /^(?:(?:(?:https?|ftp):)?\/\/)(?:\S+(?::\S*)?@)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:[/?#]\S*)?$/i.test(value);
  }
 
  return (
    <div action="/widget/wall/setting/36512/36527" className="maintainPopUp noLoader" id="wallThemeSettingsForm" encType="multipart/form-data" method="post" acceptCharset="utf-8">
      <div style={{ display: "none" }}>
        <input type="hidden" name="_method" value="POST" />
      </div>{" "}
      <div className="form-group customToolTip">
        <div className="mb-5">
          <div className="row">
            {/* <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Auto-Play Video" toggleValue={autoplayVideo}  dispatchfn={(status)=> dispatch({type:IS_THEME_SETTING_CHANGE, payload:true})}  callbackFn={setAutoplayVideo} callbackFn2={setExtra}  tooltipTxt="Videos will start playing as soon as you visit a web page that contains them." />
            </div> */}
          {/* {props.customCssStatus ==1 ? */}
            {/* <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Custom CSS" toggleValue={customCSS}  callbackFn={onChangeCustomCSS}  dispatchfn={(status)=> dispatch({type:SETTING_CUSTOM_CSS, payload:Number(status)})} callbackFn2={props.setcssStatus} tooltipTxt="Add your Custom CSS to personalize the look of your Gallery." />
            </div> */}
            {/* :''
           } */}

            {/* <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Featured Popup" toggleValue={postFeatured} callbackFn2={() => {}}  callbackFn={setPostFeatured}  dispatchfn={(status)=>{ dispatch({type:SETTING_FEATURE_POPUP, payload:Number(status)}) ; dispatch({type:CLOSE_POP_UP})} } tooltipTxt="Enables pop-up for the posts when clicked" />
            </div> */}

            {/* <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Shuffle Posts" toggleValue={shufflePost} callbackFn2={() => {}} callbackFn={setShufflePost}  dispatchfn={(status)=> dispatch({type:IS_THEME_SETTING_CHANGE, payload:true})}   tooltipTxt="Shuffles your post in random order irrespective of date/ time." />
            </div> */}

            <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Popup 'Slider'" toggleValue={popupSlider} callbackFn={setPopupSlider}  callbackFn2={setExtra } dispatchfn={(status)=> dispatch({type:SETTING_POPUP_SLIDER, payload:Number(status)})} tooltipTxt=" This feature lets you slide through the pop-up." />
            </div>

            {/* <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Mobile Popup" callbackFn2={() => {}} toggleValue={mobilePopup} callbackFn={setMobilePopup}  dispatchfn={(status)=> dispatch({type:SETTING_MOBILE_POPUP, payload:Number(status)})} tooltipTxt="Enables pop-up for the posts when clicked in web embed, in mobile version." />
            </div> */}

            {/* <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Exclude Retweets" callbackFn2={() => {}} toggleValue={excludeRetweets} callbackFn={setExcludeRetweets}  dispatchfn={(status)=> dispatch({type:IS_THEME_SETTING_CHANGE, payload:true})}   tooltipTxt="Prevents duplicacy of content by excluding Retweets." />
            </div>  */}

             <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Carousel Post" callbackFn2={() => {}} toggleValue={carouselPost}  callbackFn={setCarouselPost}  dispatchfn={(status)=>dispatch({type:SETTING_CAROUSEL_POST, payload:Number(status)})} tooltipTxt="Enable to reflect carousal posts." />
            </div>

           {[16, 50, 61].includes(commerceTheme) ?
            <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Auto Slide" callbackFn2={() => {}} toggleValue={autoSlide} callbackFn={setAutoSlide}  dispatchfn={(status)=>dispatch({type:SETTING_AUTO_SLIDER, payload:Number(status)})}  tooltipTxt="Enable the Gallery to automatically slide." />
            </div>:''}

            {/* <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Video AutoPlay" callbackFn2={() => {}} toggleValue={videoPlay} callbackFn={setVideoPlay}  dispatchfn={(status)=>dispatch({type:SETTING_VIDEO_PLAY, payload:Number(status)})}  tooltipTxt="Activating this setting will automatically play a shortened version of the video within the thumbnails. Exercise caution as this may impact your core web vitals." />
            </div>  */}
          
            {/* {[3, 4, 19, 50, 53, 61].includes(commerceTheme) ?
            <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Web Embed Filters" callbackFn2={() => {}} toggleValue={filterStatus} callbackFn={setFilterStatus}  dispatchfn={(status)=> dispatch({type:IS_THEME_SETTING_CHANGE, payload:true})}   tooltipTxt="Enables social network's filters when switched on." />
            </div> :''} */}

            {[3, 4, 19, 53].includes(commerceTheme) ?<>
            <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Show More Button"  toggleValue={loadMore}  callbackFn={setLoadMore} callbackFn2={setAutoLoad}   dispatchfn={(status)=>dispatch({type:SETTING_SHOW_MORE_BTN, loadMore:Number(status),autoLoad:Number(autoLoad ? false : autoLoad)})} tooltipTxt="Show/Hide the 'Show more' button." />  
            </div>
            <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Auto-Load" toggleValue={autoLoad}  callbackFn={setAutoLoad} callbackFn2={setLoadMore}  dispatchfn={(status)=>dispatch({type:SETTING_SHOW_MORE_BTN, autoLoad:Number(status) ,loadMore:Number(loadMore ? false : loadMore)})}  tooltipTxt="Experience the never-ending trail of feeds." />
            </div> </>:""}
            {commerceTheme==19 ?
             <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Hide 'Text Only' Posts" callbackFn2={() => {}} toggleValue={postText} callbackFn={setPostText}  dispatchfn={(status)=> dispatch({type:IS_THEME_SETTING_CHANGE, payload:true})}   tooltipTxt="Hide 'Text Only' Posts" />
            </div> :''}

            {[3, 19, 47].includes(commerceTheme) ?
             <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Trim Content" callbackFn2={() => {}} toggleValue={trimContent} callbackFn={setTrimContent}  dispatchfn={(status)=>dispatch({type:SETTING_TRIM_CONTENT, payload:Number(status)})}  tooltipTxt="It will trim post text after a limit." />
            </div> :''}
            {commerceTheme==4 ?
             <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Best Fit" callbackFn2={() => {}} toggleValue={bestFit} callbackFn={setBestFit}  dispatchfn={(status)=>dispatch({type:SETTING_BEST_FIT, payload:Number(status)})}  tooltipTxt="Enables you to load images smoothly." />
            </div> :''}
            {/* {[19, 47, 61].includes(commerceTheme) ?
            <div className="col-sm-12 mb-4">
              <ToggleBtn toggleName="Text Decoration" callbackFn2={() => {}} toggleValue={textDecoration} callbackFn={setTextDecoration}  dispatchfn={(status)=> dispatch({type:IS_THEME_SETTING_CHANGE, payload:true})}  tooltipTxt="Enables big font and double quotes for text posts." />
            </div> :''} */}
            

          
          </div>
        </div>



        <div className="mt-5">
          <div className="row align-item-center">
            <div className="col-sm-12">
              <div className="form-group">
                <label htmlFor="wallnumberOfPosts" className="mr-2"> Total Number of Posts to Display </label>
                <span className="svg-icon svg-icon-sm svg-icon-dark tooltips" data-tip="React-tooltip" data-for="totalPost">
                  <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                    <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                      <rect x="0" y="0" width="24" height="24"></rect>
                      <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10"></circle>
                      <rect fill="#000000" x="11" y="10" width="2" height="7" rx="1"></rect>
                      <rect fill="#000000" x="11" y="7" width="2" height="2" rx="1"></rect>
                    </g>
                  </svg>
                </span>
                <ReactTooltip id="totalPost" place="top" type="dark" effect="solid">
                  Maximum no. of posts to be displayed on live gallery.
                </ReactTooltip>
                <div className="totlPostDsply">
                  <input type="number" className="form-control" autoComplete="off" value={totalPost} name="data[ThemeRule][numberOfPosts]" min="1" max="100" onChange={(e) => { setTotalPost(e.target.value > 100 ? 100 :e.target.value); dispatch({type:SETTING_POST_NUMBER, payload: e.target.value > 100 ? 100 :e.target.value})}}
                  />
                </div>
              </div>
            </div>
            {[50, 61, 16].includes(commerceTheme) & autoSlide ?
            <div className="col-sm-12 slideDurationSection mb-5">
              <div className="form-group">
                <label htmlFor="wallslideDuration">
                  Time per slide (<span className="datawallslideDuration">{wallSlideDuration}</span>{" "}second)
                </label>
                <Slider onChange={(event) => { setWallSlideDuration(event) ; dispatch({type:SETTING_TIME_PER_SLIDER, payload:event})}}
                  trackStyle={{ backgroundColor: "#5540FC" }}
                  value={wallSlideDuration}
                  min={1}
                  max={10}
                />
              </div>
            </div> :''}
            
            {[4, 16, 47, 53, 61].includes(commerceTheme) ?
            <div className="col-sm-12 mb-3">
              <div className="form-group">
                <label className="control-label" htmlFor="ThemeRulenumberOfCoumn">
                {[4, 53].includes(commerceTheme) ? 'Column (Desktop)'  : ' Column (Desktop)' }
                </label>
                
                {numberOfCoumn ? <Select options={columnSelect} 
                value={columnSelect[columnSelect.findIndex(i => i.value == numberOfCoumn)]} 
                  onChange={e => { setNumberOfCoumn(`${e.value}`) ; setTotalPost(`${e.value ? e.value : 10}`) ; dispatch({type:SETTING_COLUMNS, payload:e.value ? e.value : 0});dispatch(CHANGE_WEBSITE_WALL(localStorage.getItem('active_wall'), e.value ? e.value : 10)) } } /> : null}
              </div>
            </div>  :''}
            {[4, 16, 47,  61].includes(commerceTheme) ?
             <div className="col-sm-12 mb-3">
              <div className="form-group">
                <label className="control-label" htmlFor="ThemeRulemobileColumn">
                Column (Mobile)
                </label>
                {mobileColumn ? <Select options={moblieColumnSelect}  value={moblieColumnSelect[moblieColumnSelect.findIndex(i => i.value == mobileColumn)]}   
                  onChange={e => {setMobileColumn(`${e.value}`); dispatch({type:SETTING_COLUMNS_MOBILE, payload:e.value}) }} /> : null}
              </div>
            </div>  :''}
            {commerceTheme == 16 ?
            <div className="col-sm-12">
              <div className="form-group">
                <label className="control-label" htmlFor="No of Rows">
                  No.of Rows {multiRow}
                </label>
                {multiRow ? <Select options={rowSelect} defaultValue={rowSelect[multiRow-1]}
                  onChange={e => {setMultiRow(`${e.value}`); dispatch({type:SETTING_NO_OF_ROW, payload:e.value}) }} /> : null}
              </div>
            </div> :""}
          </div>
        </div>
        {![61].includes(commerceTheme) ?
        <div className="mt-2 mb-0">
          <div className="row align-items-center">
            <div className="col-lg-12 mb-4">
              <ToggleBtn toggleName="Hide Old Posts"  dispatchfn={(status)=> dispatch({type:IS_THEME_SETTING_CHANGE, payload:true})} toggleValue={hideOld}  callbackFn={onChangeOldPost}  callbackFn2={setExtra}  tooltipTxt="Hides old posts on your live gallery." />
            </div>

            <div className="col-lg-12 mb-4" id="wallhideOlderPosts" style={{ display: hideOld ? '' : "none" }}>
              <div className="">
                {Number(showHideOlderPost) ? <Select menuPlacement="top" options={customSlelect} defaultValue={customSlelect[showHideOlderPost == 30 ? 7 : showHideOlderPost == 8 ? 8 : showHideOlderPost - 1]}
                  onChange={e => setShowHideOlderPost(e.value)} /> : null}
              </div>
            </div>
            
            <div className="col-lg-12 mb-4 hidepostDate" id="wallhideOldPosts" style={{ display: showHideOlderPost == 8 && hideOld ? "" : 'none' }}>
              <div className="input-group date dateTimePicker fgroupEndAtHide" data-target-input="nearest">
                <DateTimeField
                  language={'en'}
                  dateTime={date == 0 ? '' : minimalDate}
                  inputFormat={'MM/DD/YYYY HH:mm A'}
                  onChange={(e) => handleChange(e)}
                  viewMode={'date'}
                  className="form-control"
                  startOfWeek={'week'}
                  ref={myRefname}
                  defaultText="Select date & time"
                />
                <div onClick={(e) => { myRefname.current.onClick() }} className="input-group-append" data-target="#postcAt" data-toggle="datetimepicker">
                  <span className="input-group-text">
                  <i class="ki ki-calendar"></i>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>:''}

        
        <div className="row mb-10">
        {[3, 19, 55].includes(commerceTheme) ?
           <div className="col-sm-12 card-width-style ">
            <div className="form-group">
              <label className="d-flex align-items-center justify-content-between" htmlFor="wallminimumPostWidth">
                Minimum Post Width <span><span className="datawallminimumPostWidth">{postWidth}</span>px<span>
                </span></span></label>
              <Slider onChange={(event) => {setPostWidth(event) ; dispatch({type:SETTING_MINIMUM_POSTWIDTH, payload:event}) }}
                trackStyle={{ backgroundColor: "#5540FC" }}
                value={postWidth}
                min={150}
                max={500}
              />

              <input type="hidden" name="data[Personalization][minimumPostWidth]" value="300" className="form-control" id="wallminimumPostWidth" />					</div>
          </div>:""}

          {[3, 4 , 16, 19,  55, 61].includes(commerceTheme) ?
          <div className="col-sm-12">
            <label className="d-flex align-items-center justify-content-between" htmlFor="wallpadding">
              Post Spacing <span><span className="datawallpadding">{wallPadding}</span>px</span>
            </label>
            <Slider onChange={(event) => {setWallPadding(event); dispatch({type:SETTING_POST_SPACING, payload:event}) }}
              trackStyle={{ backgroundColor: "#5540FC" }}
              value={wallPadding}
              min={0}
              max={50}
            />
            <input type="hidden" name="data[Personalization][padding]" value="15" className="form-control" id="wallpadding" />
          </div>:""}
        </div>  

        {/* <div className="col-sm-12 mb-4">
          <ToggleBtn toggleName="Custom CSS" toggleValue={customCSS} callbackFn={onChangeCustomCSS} dispatchfn={(status) => dispatch({ type: SETTING_CUSTOM_CSS, payload: Number(status) })} callbackFn2={props.setcssStatus} tooltipTxt="Add your Custom CSS to personalize the look of your Gallery." />
        </div> */}


      </div>





      {/* <div className="modal-footer form-actions px-0">
        <div className="d-flex align-items-center justify-content-end w-100 m-0">
          <div className="card-toolbar">
            <button type="submit" onClick={() => submitData()} className={`btn btn-primary ${spinner ? `spinner spinner-white spinner-right disabled` : ''}`}>
              Apply Settings
            </button>
          </div>
        </div>
      </div> */}
      {cssUpgrade ? <CssOnOffUpgrad open={cssUpgrade} onHide={() => setCssUpgrade(false)} onBack={() => setCssUpgrade(false)} /> : <></>}
    </div >

  );
};

export default ThemeSetting;