import React, { Component } from 'react'
import { BASE_URL, WEBSITE_EMBED_URL } from '../../../../../../actions/api'

export default class preview extends Component {
    render() {
        const {dataTag, isTags} =this.props
        return (
            <div className="form-group mb-6 mb-lg-10 PreviewTags18 PreviewTagsWall"> 
                <iframe className="loadWallPreview18" src={`${WEBSITE_EMBED_URL}${localStorage.getItem('website_id')}/editor?tags=${isTags}&keywords=${dataTag}&website=true`}></iframe>
            </div>
        )
    }
}
