import React, { Component } from 'react'

export default class LoadingScreen extends Component {
    render() {
        return (
            <div id="preloader_" className="loader-display-auth" style={{zIndex:'999'}}>
                <div className="d-flex align-items-center justify-content-center h-100 w-100">
                    <img className="img-fluid" height="72" width="72" src="https://cloud.taggbox.com/taggshop/app/assets/media/logos/taggshop-preloader.gif" alt="" />
                </div>
            </div>
        )
    }
}
