import { useEffect, useState } from "react"; 
import {  Dashboard } from "@uppy/react";
import { useDispatch } from "react-redux";
import { addBackground } from "../../../../../../actions/customization";
import { CHANGE_BACKGROUND_COLOR, CHANGE_BACKGROUND_STATUS , CHANGE_BACKGROUND_IMAGE, IS_BACKGROUN_CHANGE} from "../../../../../../reducer/themes/reducersKeys";
import ColorComp from "./elements/color";
import UppyUpload from "../../../../../../utils/uppyUpload";
 
const uppyUploader = new UppyUpload()
  const Background = ({ backgroundDetails, openBanner, PersonalizationID , bannerDetail, save_and_publish, is_backgroun_change}) => {
  const dispatch = useDispatch()
  const [transparentBg, setTransparentBg] = useState(backgroundDetails?.Background?.transparent)
  const [selectedFile, setSelectedFile] = useState(backgroundDetails?.Background?.image ? backgroundDetails?.Background?.image :"");
  const [filename, setFilename] = useState();
  const [selected, setSelected] =  useState(backgroundDetails?.Background?.image ? backgroundDetails?.Background?.image :"");
  const [bgColor, setBgColor] = useState(backgroundDetails?.Background?.color ? backgroundDetails.Background?.color : '#ffffff')
  const [loading, setLoading] = useState(false)
  const [bgRemove, setBgRemove] = useState(1)

  useEffect(() => {
    uppyUploader.uppy.on("transloadit:result", (stepName, result) => { 
      setSelectedFile(`${result.id}.${result.ext}`);
      setFilename(`https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`);
      setSelected(`https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`);
      dispatch({ type:CHANGE_BACKGROUND_IMAGE, payload:`https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`})
      setTimeout(() => { uppyUploader.uppy.cancelAll(); }, 300);
    });

  }, []);
 
  useEffect(() => { 
    if(save_and_publish==true && is_backgroun_change==true)  onApplyBackground(); 
  }, [save_and_publish]);


  const onApplyBackground = () => {
    const backgroundObj = { isCollaborator: 0, id: PersonalizationID, transparent: transparentBg ? 1 : 0, backgroundColor: bgColor, backgroundRemove: selected.length ? 0 : bgRemove, themeId: backgroundDetails.ThemeRule.themeId, collaboratorOwnerId: 76636, ownerId: localStorage.getItem('ownerId'), picture: selected ? `https://cloud.tagshop.ai/tmp/${selectedFile}` : '', isPictureChanged: selected == selectedFile ? 0 : 1, };
   addBackground(backgroundObj).then((res) => { })
  }


  return (
    <div className="noLoader pt-2" >
      <div style={{ display: "none" }}> <input type="hidden" name="_method" value="POST" /> </div>
      <div className="row align-items-center">
        <div className="col-sm-12">
          <div className="form-group mb-0">
            <div className="row align-items-center">
              <div className="col-12">
                <div className='form-group mb-0'>
                  <label className="control-label">Transparent Background</label>
                </div>
                <div className="toggleOnBut">
                  <span className="switch"> <label> <input type="checkbox" className="onoffswitch-checkbox updateStatus" id="walltransparent" name="data[Wall][transparent]" defaultValue={transparentBg} checked={transparentBg} onChange={(e) => {setTransparentBg(e.target.checked);  dispatch({ type:CHANGE_BACKGROUND_STATUS, payload:Number(e.target.checked)}); dispatch({ type: IS_BACKGROUN_CHANGE, payload: true }); }} /> <span></span> </label> </span>
                </div>
              </div>
            </div>
          </div>
        </div>

        {!transparentBg ? <div className="col-sm-12 backgroundOptions mt-3" >
            <div className="form-group">
              <label className="control-label mb-1">Color</label>
              <div className="minicolors minicolors-theme-bootstrap minicolors-position-bottom minicolors-position-left">
                <ColorComp id="bgColor" defaultColor={bgColor} changeColor={(clr) => { dispatch({ type:CHANGE_BACKGROUND_COLOR, payload:clr}); dispatch({ type: IS_BACKGROUN_CHANGE, payload: true }); setBgColor(clr)} } />
              </div>
            </div>
        </div> : null}
      </div>

      {!transparentBg ?

        (<div className="row backgroundOptions">
          <div className="col-sm-12">
            <div className="form-group">
              <label className="clearfix control-label">Background Image</label>
              <div className="row">
                {selected ? <div className="fileinput fileinput-new col-md-12 new-upload-box" data-provides="fileinput" >
                  <div className="row">
                    <div className="col-12">
                      <div className="image-input fileinput fileinput-new addProUploder addbgUpload uppy_upload ml-0 uppy-btn" data-provides="fileinput" style={{ display: "block" }} >
                        <div id="backgroundImage" className="backgroundImage align-content-center flex-wrap justify-content-center mb-0 border-bottom-0" data-trigger="fileinput" style={{ backgroundImage: `url(${selected})` }} >
                          <button type="button" id="removeWallBackground" className="removeWallBackground btn btn-xs btn-icon btn-circle btn-white btn-shadow" onClick={() => {setSelected(''); dispatch({ type:CHANGE_BACKGROUND_IMAGE, payload:''}); dispatch({ type: IS_BACKGROUN_CHANGE, payload: true });}} > <i className="ki ki-close icon-xs"></i> </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                  : <Dashboard proudlyDisplayPoweredByUppy={false} hideUploadButton={true} showProgressDetails={true} disableThumbnailGenerator={false} height={270} showRemoveButtonAfterComplete={false} uppy={uppyUploader.uppy} plugins={["Instagram", "GoogleDrive", "Url", "Webcam"]} /> }
              </div>
            </div>
          </div>
        </div>)
        : null
      }

      {!transparentBg ? <div className="pt-4">
        <div className="aspectRatio"> Recommended Size: 1920px x 1080px Image with Aspect Ratio and Resolution of Intended Display. </div>
      </div> : null}
    </div>
  );
};

export default Background;
