const mapStateToProps = (state) => {
    return {
      appData:state.appData
    }
}
const mapDispatchToProps = dispatch => {
    return {
     CHANGE_THEMES:(id)=> dispatch(CHANGE_THEME_ACTION(id)),
     APPLY_THEME:(id, callback)=> dispatch(APPLY_THEME_ACTION(id,callback))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(allthemes);