export const getAppsMOreDetails = () => {
    const {user_data} = store.getState().authorized?.userData
    var extraData = user_data?.user?.extra_data ? JSON.parse(user_data?.user?.extra_data) : {}
    var wall_id = localStorage.getItem('wallId') ? localStorage.getItem('wallId') : extraData?.wallId;
    return (dispatch) => {
        new HttpClient()
            .post(`${MODERATION_API_GET_DETAILS}`, {
                ownerId: localStorage.getItem('ownerId'),
                wallId: wall_id,
            })
            .then((detailReponse) => {
                // TOAST_DISMISS();
                const { wall } = detailReponse.data;


                dispatch({
                    type: APPS_MORE_BANNER_DETAIL,
                    payload: detailReponse.data,
                });
            });
    };
};






