export const addCollaborator = ({ emailId, collaboratorRole }) => {
    const userData  = store.getState().moderation.details;

    if (userData && Object.keys(userData).length > 0) {
        return new HttpClient()
            .post(`${Add_Collaborator}/${localStorage.getItem('wallId')}`, { wallName: userData.wall.name, emailId: emailId, collaboratorRole: collaboratorRole })
    }
    else {
        return null
    }


}

