export const getNetworklist = (planId, type) => {
    const { userData } = store.getState().authorized;
    var planIdUpdate = planId
    if (userData && Object.keys(userData).length > 0 && userData.user_data && Object.keys(userData.user_data).length > 0) {
        planIdUpdate = userData.user_data.UserPlan.planId
    }
    const dataUpdate = type == 4 ? { wallId: localStorage.getItem('wallId') } : {}
    return new HttpClient().post(`${GET_NETWORK_LIST}`,
        {
            'type': type,
            'planId': `${planIdUpdate}`,
            ...dataUpdate
        }
    );
}

