export const customPostsubmit = (ownerId, wallId, data) => {

    const config = {
        headers: {
            'authorization': `Bearer ${localStorage.getItem("token")}`,
            "content-type": "multipart/form-data"
        }
    };

    if (Object.fromEntries(data).eid) {
        return axios.post(`${STATUS_UPDATE_CUSTOM_POST}/${wallId}/${Object.fromEntries(data).eid}`, data, config);
    } else {
        return axios.post(`${CREATE_CUSTOM_POST}/${wallId}`, data, config);
    }
}



