import React, { useEffect } from 'react'
import BigcommercePublish from './elements/bigcommercePublish';
import { connect } from 'react-redux';
import { GET_BIGCOMMERCE_PUBLISH_DATA } from '../../../actions/bicommerceAction';
import ErrorFound from '../../ErrorFound';
import LoadingScreen from '../../pages/LoadingScreen';
import { useSelector, useDispatch } from 'react-redux';
import { useParams } from 'react-router-dom';

const Index = () => {
    const BigcommerceData = useSelector(state => state.BigcommerceData.BigcommerceData);
    const dispatch = useDispatch();
    const { id } = useParams()

    useEffect(() => {
        dispatch(GET_BIGCOMMERCE_PUBLISH_DATA());
    }, [dispatch]);

    useEffect(() => {
        dispatch(GET_BIGCOMMERCE_PUBLISH_DATA());
    }, [id]);

    return BigcommerceData ? <ErrorFound><BigcommercePublish/></ErrorFound> : <LoadingScreen />;
}

export default Index;
