import React, { PureComponent } from "react"; 
import { Link } from "react-router-dom";

export default class Error204 extends PureComponent {
  onClicktoUrl = (path) => event => {
    window.top.location.href = path;
}
  render() {
    const { wall } = this.props;
    return (
      <div className="ts_error_content">
        <div className="ts_error_title">No post found in Public!</div>
        <div className="ts_error_des">It seems that all the posts are in private.
        <Link style={{ cursor: 'pointer' }} to={`/content/moderation/${localStorage.getItem('wallId')}`} className="manage-feed"> Click here</Link> to make them public.</div>
      </div>
    );
  }
}
